import React from "react";

const NotFound = props => {
	return (
		<div className="ui image" style={{ width: "100%" }}>
			<img
				src="/img/not-found.png"
				alt="page not found"
				style={{ width: "100%" }}
			/>
		</div>
	);
};

export default NotFound;



